import React, { useContext, useState} from 'react';
import MyContext from "./Context/MyContext";
import ReactPlayer from 'react-player/lazy'
import screenfull from 'screenfull';
import 'remixicon/fonts/remixicon.css'
import { useNavigate } from 'react-router-dom';

function Main() {
  const navigator = useNavigate();
  const context = useContext (MyContext);
  const {selfid,peer,selfstream,theirstream,settheirid,senddata,chat,setdata,onclikaudio,onclikvideo}=context;
  const [mode, setmode] = useState("Full Screen")
  let a=1;
  const [b, setb] = useState(0)
  const flex={display:"flex"};
  const none={display:"none"};


const onClick = ()=>{
  navigator.clipboard.writeText(selfid)
  alert("Room id copied")
}

// const sendMessage= ()=>{
//   senddata();
// }

const onClickFullScreen = (e)=>{
  e.preventDefault();
if(a===1){
  screenfull.request()
  setmode("esc to exit full screen")
  a=0
}else if(a===0){
  setmode("Full Screen")
  a=1
}
}

const endcall = ()=>{
  navigator("/");
}
  return (
    <div  className="h-screen w-screen flex font-Roboto">
      <div className="h-full w-full relative bg-black">
          <div className='h-auto absolute w-full bg-[#776B5D] px-5 py-1 flex items-center gap-3 justify-between'>
         <div className='flex items-center gap-3'>
         <button onClick={onClick} className='border px-2 text-sm rounded-xl font-semibold text-[#F5F5F5] border-[#F5F5F5]'>Room id: {selfid? selfid : "loading..."}</button> |
          <p className='text-xs text-white'>Send your room id to your friend and then fun with your friend</p>
         </div>
          <button onClick={onClickFullScreen} className='border px-2 text-sm rounded-xl  font-semibold  text-[#F5F5F5] border-[#F5F5F5]'>{mode}</button> 
          </div>

        <div className='h-full bg-black  flex justify-center pt-10  rounded-xl'>
          <ReactPlayer  url="https://youtu.be/aRzbHxJZSTo?si=YNC47pU6a7mbvQWt" height="90%" width="100%"  volume={null} />
        </div>

      </div>

      {/* bg-[#DE8971] */}

      <div className="h-full w-1/4 bg-[#776B5D] flex flex-col gap-1 p-1 pt-8">
        <div className="h-1/3 w-full relative">
          <video ref={theirstream}  autoPlay={true} className=" w-full   bg-red-900  rounded-xl -scale-x-100 " />
</div>
        <div onMouseEnter={()=>{setb(1)}} onMouseLeave={()=>{setb(0)}} className="h-1/3 w-full relative">
          {/* Add another video element for the remote stream if needed */}
          <video  ref={selfstream} autoPlay={true} className=" w-full bg-red-900 rounded-xl -scale-x-100 " />
          <div style={b===1? flex : none} onMouseEnter={()=>{setb(1)}} onMouseLeave={()=>{setb(0)}} className='absolute bottom-0 mb-2 text-2xl  justify-evenly w-full'>
            <i onClick={onclikvideo} class="ri-camera-off-fill"></i>
            <i onClick={onclikaudio} class="ri-mic-off-fill"></i>
            <i onClick={endcall} class="ri-arrow-go-back-line"></i>
          </div>
</div>
<div className='h-1/3 w-full relative p-2 bg-black font-Roboto'>

<div className='overflow-y-scroll  h-52 '>
<p className='text-white  border-white overflow-auto max-w-2/3 w-fit px-1 rounded-md  text-start mb-1'> Hello Kundan Kaise ho!</p>
<div className='flex justify-end'><p className='text-white  border-white overflow-auto max-w-2/3  px-1 rounded-md  text-end'> Mai thik hu...</p></div>
</div>

<div className='absolute  bottom-0 w-[19vw]  flex gap-1 pb-2'>
<input className=' w-full text-white font-Roboto bg-transparent outline-none' placeholder='Enter message' /> <button  className='text-white border rounded-md px-1'>Send</button>
</div>

</div>
      </div>
    </div>
  );
}

export default Main;
