import React, { useRef, useState } from 'react';
import MyContext from './MyContext';
import { Peer } from "peerjs";
import cors from"cors";

function Context(props) {

  cors()

const [selfid, setselfid] = useState("");
const [theirid, settheirid] = useState("");
const selfstream = useRef([]);
const theirstream = useRef([]);
const [chat, setchat] = useState([]);
const [alertmessage, setalertmessage] = useState("")
const [data, setdata] = useState("")
const [whataudio, setwhataudio] = useState(true)
const [whatvideo, setwhatvideo] = useState(true)

const peer = new Peer();

//logic for video
const onclickvideo=()=>{
  var a = 1;
  if(a===0){
    setwhatvideo(true)
    a=1
  }else if(a===1){
    setwhatvideo(false)
    a=0
  }
}


//logic for audio
const onclickaudio=()=>{
  var a = 1;
  if(a===0){
    setwhataudio(true)
    a=1
  }else if(a===1){
    setwhataudio(false)
    a=0
  }
}

//chatting logic
const senddata = ()=>{
var conn = peer.connect(theirid);
conn.on("data",data)
// conn.send({selfid:data})
}

// conn.on("data",(data)=>{
  // var a = chat;
   // setchat(...a,data.data)
  // console.log(data)
// })

  //connecting to peer

const connectpeer = ()=>{
  console.log("getting id")
  peer.on("open",(id)=>{
    setselfid(id)
    console.log("got sucess")
  })
}
console.log(selfid);

//calling to a person 
const startcall  =async ()=>{
  try{
    var mediaStream = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
    await mediaStream({video:{whatvideo},audio:{whataudio}}, (stream)=>{
      var call = peer.call(theirid,stream);    
      selfstream.current.srcObject = stream ;
      call.on('stream',(stream)=>{
        theirstream.current.srcObject = stream
      })
    })
  }catch(error){
  console.log(error)
}
}

  // Answering a call 

  peer.on('call',async (call)=>{
    console.log("incoming call")
    var mediaStream = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
    await mediaStream({video:{whatvideo},audio:{whataudio}}, (stream)=>{
    call.answer(stream)
    selfstream.current.srcObject = stream
    })
    console.log("call recived")
    call.on('stream',(stream)=>{
      theirstream.current.srcObject = stream
    })
  })

    

      return (
        <MyContext.Provider  value={{connectpeer,selfid,peer,theirid,settheirid,peer,selfstream,theirstream,startcall,alertmessage,setalertmessage,chat,setdata,senddata,onclickaudio,onclickvideo}}>
        {props.children}
        </MyContext.Provider>
      )
    }
    
    export default Context;
    